export class Bill {
    logo: string;
    vendorName: string = 'RAJNANDANI HP GAS GRAMIN VITRAK';
    village: string = "MAINARAHIKA BAJITPUR";
    post: string = "MANIGACHHI";
    dist: string = "DARBHANGA (847422)";
    contactNo: string = "9546081633, 8271608018";
    customerInfo: CustomerInfo;
    items: Items[] = [];
    otherDetails: string;
    skills: Skill[] = [];

    constructor() {
        this.customerInfo = new CustomerInfo();
        this.items.push(new Items());
        this.skills.push(new Skill());
    }
}

export class CustomerInfo {
    name: string;
    address: string;
    contactNumber: string;
}

export class Items {
    date: string;
    description: string;
    unitPrice: any;
    qty: number;
    total: any;
    tax: any;
    taxAmount: any;
    totalAmountWithTax: any
}

export class Skill {
    value: string;
}
