import { Component } from '@angular/core';
import { Bill, CustomerInfo, Items, Skill } from './bill';
import { ScriptService } from './script.service';
import { Router } from '@angular/router';

import { AuthenticationService, UserService } from '../_services';
import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
pdfMake.vfs = pdfFonts.pdfMake.vfs;
@Component({
  selector: 'home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.css']
})
export class HomeComponent {

  bill = new Bill();

  degrees = ['B.E.', 'M.E.', 'B.Com', 'M.Com'];

  constructor(private scriptService: ScriptService,      private router: Router,
    private authenticationService: AuthenticationService) {
    this.bill = JSON.parse(sessionStorage.getItem('bill')) || new Bill();
    this.bill.logo = `data:image/png;base64,
    
    
    `
    if (!this.bill.items || this.bill.items.length === 0) {
      this.bill.items = [];
      this.bill.items.push(new Items());
    }
    if (!this.bill.skills || this.bill.skills.length === 0) {
      this.bill.skills = [];
      this.bill.skills.push(new Skill());
    }

    console.log('Loading External Scripts');
    this.scriptService.load('pdfMake', 'vfsFonts');
  }

  // addCustomerInfo() {
  //   this.bill.customerInfo.push(new CustomerInfo());
  // }

  addItems() {
    this.bill.items.push(new Items());
  }
  removeItems() {
    this.bill.items.splice(-1,1);
  }
  logout() {
    this.authenticationService.logout();
    this.router.navigate(['/login']);
   }
  calculate(i) {
    let item = this.bill.items[i];
    let totalPrice = item.unitPrice * item.qty;
    let taxAmount = totalPrice * item.tax /100;
    let totalWithTax = totalPrice + taxAmount;
    this.bill.items[i].total = totalPrice;
    this.bill.items[i].taxAmount = taxAmount;
    this.bill.items[i].totalAmountWithTax = totalWithTax;

  }

  generatePdf(action = 'open') {
    console.log(pdfMake);
    const documentDefinition = this.getDocumentDefinition();

    switch (action) {
      case 'open': pdfMake.createPdf(documentDefinition).open(); break;
      case 'print': pdfMake.createPdf(documentDefinition).print(); break;
      case 'download': pdfMake.createPdf(documentDefinition).download(); break;

      default: pdfMake.createPdf(documentDefinition).open(); break;
    }

  }


  resetForm() {
    this.bill = new Bill();
  }

  getDocumentDefinition() {
    console.log(this.bill)
    sessionStorage.setItem('bill', JSON.stringify(this.bill));
    return {
      content: [
        {
          text: 'Bill-Invoice',
          bold: true,
          fontSize: 20,
          alignment: 'center',
          margin: [0, 0, 0, 20]
        },
        {
          columns: [
            [{
              text: this.bill.vendorName,
              style: 'name'
            },
            {
              text: 'VILL:- ' +this.bill.village.toLocaleUpperCase()
            },
            {
              text: 'PS:- ' +this.bill.post.toLocaleUpperCase()
            },
            {
              text: 'DIST:- ' +this.bill.dist.toLocaleUpperCase()
            },
            {
              text: 'MO:- ' +this.bill.contactNo
            }
            ],
            [
              this.getProfilePicObject()
            ]
          ]
        },
        {
          text: '_____________________________________  Bill Invoice _______ ',
          bold: true,
          fontSize: 20,
          alignment: 'center',
          margin: [0, 0, 0, 20]
        },
        {
          text: 'Bill To',
          style: 'header'
        },
        this.getCustomerInfoObject(this.bill.customerInfo),
        {
          text: '',
          style: 'header'
        },
        this.getEducationObject(this.bill.items),
        {
          text: 'Note*',
          style: 'noteHeader'
        },
        {
          text: this.bill.otherDetails,
          style: 'noteText'
        },
        {
          text: 'Signature',
          style: 'sign'
        }
      ],
        styles: {
          header: {
            fontSize: 18,
            bold: true,
            margin: [0, 20, 0, 10],
            decoration: 'underline',
            width: '100%'
          },
          noteHeader: {
            fontSize: 12,
            bold: true,
            margin: [0, 50, 0, 10],
            decoration: 'underline',
            width: '100%'
          },
          noteText: {
            fontSize: 10,
            italics: true
          },
          name: {
            fontSize: 12,
            bold: true
          },
          customerTitle: {
            fontSize: 14,
            bold: true,
            italics: true
          },
          sign: {
            margin: [0, 50, 0, 10],
            alignment: 'right',
            italics: true
          },
          tableHeader: {
            bold: true,
          }
        }
    };
  }

  getCustomerInfoObject(customerInfo: CustomerInfo) {

    const exs = [];
      exs.push(
        [{
          columns: [
            [{
              text: customerInfo.name,
              style: 'customerTitle'
            },
            {
              text: 'ADDRESS:- '+customerInfo.address,
            },
            {
              text: 'PHONE:- '+customerInfo.contactNumber,
            }
          ],
            {
              text: 'Customer info ',
              alignment: 'right'
            }
          ]
        }]
      );

    return {
      table: {
        widths: ['*'],
        body: [
          ...exs
        ]
      }
    };
  }

  getEducationObject(items: Items[]) {
    return {
      table: {
        body: [
          [{
            text: 'DATE',
            style: 'tableHeader'
          },
          {
            text: 'Item Description',
            style: 'tableHeader'
          },
          {
            text: 'Unit Price (Rs.)',
            style: 'tableHeader'
          },
          {
            text: 'Qty',
            style: 'tableHeader'
          },
          {
            text: 'Total',
            style: 'tableHeader'
          },
          {
            text: 'Tax %',
            style: 'tableHeader'
          },
          {
            text: 'Tax Amount (Rs.)',
            style: 'tableHeader'
          },
          {
            text: 'Total (Inc tax) Rs.',
            style: 'tableHeader'
          },
          ],
          ...items.map(item => {
            return [item.date, item.description, item.unitPrice, item.qty, item.total, item.tax, item.taxAmount, item.totalAmountWithTax];
          })
        ]
      }
    };
  }

  getProfilePicObject() {
    if (this.bill.logo) {
      return {
        image: this.bill.logo,
        width: 75,
        alignment : 'right'
      };
    }
    return null;
  }
  addSkill() {
    this.bill.skills.push(new Skill());
  }

}
